<template>
<div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel" ref="marquee">
  <div class="carousel-inner text-white">
    <div class="carousel-item active">
      <p><span class="badge bg-secondary"></span>  8:2022First</p>
    </div>
    <div class="carousel-item">
      <p><span class="badge bg-secondary"></span>   <a href="#" class="text-muted" @click.prevent="toCustom">More...</a></p>
    </div>
  </div>
</div>
</template>

<script>
import Carousel from 'bootstrap/js/dist/carousel'
export default {
  data () {
    return {
      marquee: {}
    }
  },
  methods: {
    toCustom () {
      this.$router.push('/custom')
    }
